import {AttrEntries} from "@/model/grade/grade-config";
import {convertAttrToGrade} from "@/model/grade/grade-calc";
import {ATTRS} from "@/model/grade/attr";

export class SkillAndConfig {
    constructor(skillConfigs, auraRange) {
        this.skillConfigs = {}
        skillConfigs.forEach(skillConfig => {
            this.skillConfigs[skillConfig['skillType']] = skillConfig
        })
        this.auraRange = auraRange || 0
    }
    getAuraRange() {
        return this.auraRange
    }
}

export class SkillAttrGroup {
    constructor(options) {
        if (options) {
            this.mainAttr = options.mainAttr;
            this.viceAttr = options.viceAttr;
            this.teamAttr = options.teamAttr;
            this.auraRange = options.auraRange || 0;
        }
    }
    calcGrade() {
        const ret = {totalGrade: 0}
        if (this.mainAttr) {
            ret['mainGrade'] = convertAttrToGrade(this.mainAttr)
            ret['totalGrade'] += ret['mainGrade'].calcTotalGradePoint()
        }
        if (this.viceAttr) {
            ret['viceGrade'] = convertAttrToGrade(this.viceAttr)
            ret['totalGrade'] += ret['viceGrade'].calcTotalGradePoint()
        }
        if (this.teamAttr) {
            ret['teamGrade'] = convertAttrToGrade(this.teamAttr, calcTeamGradeFactor(this.auraRange))
            ret['totalGrade'] += ret['teamGrade'].calcTotalGradePoint()
        }
        return ret
    }
    multiple(factor) {
        if (this.mainAttr) {
            this.mainAttr.multiple(factor)
        }
        if (this.viceAttr) {
            this.viceAttr.multiple(factor)
        }
        if (this.teamAttr) {
            this.teamAttr.multiple(factor)
        }
    }
}

export function ConvertSkillToAttrGroup(skillAndConfig, options) {
    options = options || {}
    const mainAttr = new AttrEntries()
    const viceAttr = new AttrEntries()
    const teamAttr = new AttrEntries()

    // 此处规则复杂
    // 第一种情况：该技能只有一条配置
    // 若技能配置类型为团队天赋或团队状态，则同时填充至个人和团队属性中；否则只填充至个人属性中
    // 第二种情况：该技能有多条配置（目前只可能有两条）
    // 又有三种组合
    // 1. 装备技+状态组合，都是防功。处理方法是忽略装备技效果
    // 2. 团队天赋+团队状态组合，都是一些既有个人效果又有团队效果的天赋。
    //    处理方法是：将团队天赋配置填充至个人属性和团队属性中，将团队状态配置填充至团队属性中
    // 3. 状态+状态组合。仅有一个孤例（1004日月凌空）。怀疑是狐狸配错了。处理方法是忽略第一条状态配置
    if (Object.values(skillAndConfig.skillConfigs).length === 0) {
        // 无配置
    }
    else if (Object.values(skillAndConfig.skillConfigs).length === 1) {
        Object.entries(skillAndConfig.skillConfigs).forEach(([key, value]) => {
            fillSkillConfigToAttrSet(value, mainAttr, 'normal', options)
            if (key == '团队天赋') {
                fillSkillConfigToAttrSet(value, teamAttr, 'team_talent', options)
            } else if (key == '团队状态') {
                fillSkillConfigToAttrSet(value, teamAttr, 'normal', options)
            }
        })
    } else if (Object.values(skillAndConfig.skillConfigs).length === 2) {
        if (skillAndConfig.skillConfigs['装备技'] && skillAndConfig.skillConfigs['状态']) {
            // 忽略装备技效果
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['状态'], mainAttr, 'normal', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['装备技'], viceAttr, 'normal', options)
        } else if (skillAndConfig.skillConfigs['团队天赋'] && skillAndConfig.skillConfigs['团队状态']) {
            // 将团队天赋配置填充至个人属性中，将团队状态配置填充至团队属性中
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队天赋'], mainAttr, 'normal', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队天赋'], teamAttr, 'team_talent', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队状态'], teamAttr, 'team_buff', options)
        } else {
            throw new Error('技能配置数量异常');
        }
    } else {
        throw new Error('技能配置数量异常');
    }
    return new SkillAttrGroup({
        mainAttr, viceAttr, teamAttr, auraRange: skillAndConfig.getAuraRange()
    })
}

function fillSkillConfigToAttrSet(sc, as, typ, options) {
    // 普通设置函数，不乘系数，护体直接加倍
    const f = (key, value) => {
        if (value > 0) {
            if (options.upFlag) {
                value *= 2
            }
            as.setValue(key, value)
        }
    }
    // 加法函数，乘系数，护体直接加倍
    const af = (key, value) => {
        if (value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value *= 2
            }
            as.setValue(key, value)
        }
    }
    // 乘法函数，乘系数，护体与1的差值加倍
    const pf = (key, value) => {
        if (value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value = value * 2 - 1
            }
            as.setValue(key, value)
        }
    }
    const caf = (cls, key, value) => {
        if (cls && value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value *= 2
            }
            as.setClassValue(cls, key, value)
        }
    }
    const cpf = (cls, key, value) => {
        if (cls && value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value = value * 2 - 1
            }
            as.setClassValue(cls, key, value)
        }
    }

    if (typ == 'normal' || typ == 'team_talent') {
        pf('裸攻', sc['bodyAtk'])
        pf('裸防', sc['bodyDef'])
        pf('裸轻', sc['bodyAgi'])
        pf('场攻', sc['battleAtk'])
        pf('场防', sc['battleDef'])
        pf('场轻', sc['battleAgi'])
        pf('时序', sc['battleAsp'])
        pf('敌攻', sc['enemyAtk'])
        pf('敌防', sc['enemyDef'])
        pf('敌轻', sc['enemyAgi'])
        pf('敌时', sc['enemyAsp'])
        af('双击', sc['doubleHit'])
        af('暴击', sc['critical'])
        f('移动', sc['moveDistance'])
        f('射程', sc['attackDistance'])
        f('范围', sc['attackRange'])
        af('出招', sc['castInc'])
        caf(sc['specCastIncType'], '出招', sc['specCastInc'])
    }
    if (typ == 'normal' || typ == 'team_buff') {
        pf('命中', sc['hitInc'])
        pf('闪避', sc['hitDec'])
        pf('威力', sc['powerInc'])
        pf('减威', sc['powerDec'])
        pf('伤害', sc['damageInc'])
        pf('减伤', sc['damageDec'])
        cpf(sc['specHitIncType'], '命中', sc['specHitInc'])
        cpf(sc['specHitDecType'], '闪避', sc['specHitDec'])
        cpf(sc['specPowerIncType'], '威力', sc['specPowerInc'])
        cpf(sc['specPowerDecType'], '减威', sc['specPowerDec'])
        cpf(sc['specDamageIncType'], '伤害', sc['specDamageInc'])
        cpf(sc['specDamageDecType'], '减伤', sc['specDamageDec'])
        pf('加受伤', sc['injuryInc'])
        pf('减受伤', sc['injuryDec'])
        af('吸血', sc['bloodSuck'])
        af('反弹', sc['damageCounter'])
        af('挪移', sc['damageTransfer'])
        af('反震', sc['damageReflect'])
        if (sc['deBuffImmune'] && sc['deBuffImmune'].trim() != '') {
            sc['deBuffImmune'].split(' ').map(x => x.trim())
                .forEach(x => as.addImmune(x))
        }
        if (sc['deBuffMake'] && sc['deBuffMake'].trim() != '') {
            let rate = sc['deBuffRate']
            if (options.upFlag) {
                rate *= 2
            }
            const round = sc['deBuffRound']
            sc['deBuffMake'].split(' ').map(x => x.trim())
                .forEach(x => as.addDeBuffMake(x, rate, round))
        }
    }
    // ext
    const ext = sc['ext']

    if (ext) {
        if (ext['values']) {
            ext['values'].forEach(v => {
                const attrConf = ATTRS[v['attr']]
                if (attrConf) {
                    let value = v['value']
                    if (options.upFlag) {
                        if (attrConf.pct) {
                            value = value * 2 - 1
                        } else {
                            value = value * 2
                        }
                    }
                    if (attrConf.calcFunc) {
                        as.setExtValue(v['attr'], value)
                    } else {
                        as.setValue(v['attr'], value)
                    }
                }
            })
        }
        if (ext['flags']) {
            ext['flags'].forEach(v => {
                const attrConf = ATTRS[v]
                if (attrConf) {
                    if (attrConf.calcFunc) {
                        as.setExtValue(v, 1)
                    }
                }
            })
        }
        if (ext['debuffs']) {
            ext['debuffs'].forEach(v => {
                let rate = v['rate']
                if (options.upFlag) {
                    rate *= 2
                }
                as.addDeBuffMake(v['attr'], rate*100, v['value']*100)
            })
        }
        if (ext['immunes']) {
            ext['immunes'].forEach(v => {
                as.addImmune(v)
            })
        }
    }
}

function calcTeamGradeFactor(auraRange) {
    if (auraRange == 0) {
        return 3
    }

    const dis = auraRange*2 + 1
    return (dis*dis-1) / 800.0
}