import store from "@/store";

export function searchHero(id) {
    return store.state.HeroSource[id];
}

export function searchHeroText(text) {
    // 正则表达式匹配：[{id}]{content}
    const id = text.match(/\[(\d+)\]/)[1];
    return store.state.HeroSource[id];
}

export function searchSkill(id) {
    return store.state.SkillSource[id];
}

export function searchSkillText(text) {
    // 正则表达式匹配：[{id}]{content}
    const id = text.match(/\[(\d+)\]/)[1];
    return store.state.SkillSource[id];
}

export function searchItem(id) {
    return store.state.ItemSource[id];
}

export function searchItemText(text) {
    // 正则表达式匹配：[{id}]{content}
    const id = text.match(/\[(\d+)\]/)[1];
    return store.state.ItemSource[id];
}

export function searchSuit(wp, ar, pt) {
    const key = `${wp}-${ar}-${pt}`;
    return store.state.SuitIndex[key];
}

export function searchTitle(id) {
    return store.state.TitleIndex[id];
}

export function searchSkillConfigs(id) {
    return store.state.SkillConfigIndex[id]
}

export function searchSkillExtConfig(id) {
    return store.state.SkillExtConfigSource[id]
}

export function bindSkillAndExt() {
    Object.entries(store.state.SkillExtConfigSource).forEach(([id, ext]) => {
        const configs = searchSkillConfigs(id) || [];
        const attrs = ext['attrs']
        attrs.forEach(attr => {
            const config = configs.find(config => config['skillType'] === attr['type'])
            if (config) {
                config['ext'] = attr
            } else {
                const skill = searchSkill(id)
                const newConfig = {
                    name: skill['name'],
                    skillId: id,
                    skillType: attr['type'],
                    ext: attr
                }
                store.state.SkillConfigSource.push(newConfig)
                store.state.SkillConfigIndex[id].push(newConfig)
            }
        })
    });
}

export function searchFriendConf(id) {
    return store.state.Const.friend[id];
}